import React, { useState } from 'react';
import { Container, Paper, TextField, Button, Typography, Box, Alert } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { authService, RegisterResponse } from '../services/api';

const Register: React.FC = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError('');
        setSuccess('');

        // ifre kontrol
        if (password !== confirmPassword) {
            setError('ifreler elemiyor.');
            return;
        }

        // Email format kontrol
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!emailRegex.test(email)) {
            setError('Geerli bir email adresi giriniz.');
            return;
        }

        // ifre uzunluu kontrol
        if (password.length < 6) {
            setError('ifre en az 6 karakter olmaldr.');
            return;
        }

        setLoading(true);

        try {
            const response = await authService.register(email, password);

            if (response.data) {
                setSuccess('Kaydnz baaryla oluturuldu! Giri sayfasna ynlendiriliyorsunuz...');
                setTimeout(() => {
                    navigate('/login');
                }, 2000);
            }
        } catch (err: any) {
            console.error('Kayt hatas:', err);
            if (err.response?.data?.message) {
                setError(err.response.data.message);
            } else if (err.message) {
                setError(err.message);
            } else {
                setError('Kayt olurken bir hata olutu. Ltfen tekrar deneyin.');
            }
        } finally {
            setLoading(false);
        }
    };

    return (
        <Container maxWidth="sm">
            <Box sx={{ mt: 8, mb: 4 }}>
                <Paper elevation={3} sx={{ p: 4 }}>
                    <Typography variant="h4" component="h1" gutterBottom align="center">
                        Kayt Ol
                    </Typography>
                    {success && (
                        <Alert severity="success" sx={{ mb: 2 }}>
                            {success}
                        </Alert>
                    )}
                    {error && (
                        <Alert severity="error" sx={{ mb: 2 }}>
                            {error}
                        </Alert>
                    )}
                    <form onSubmit={handleSubmit} noValidate>
                        <TextField
                            fullWidth
                            label="Email"
                            type="email"
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            margin="normal"
                            required
                            disabled={loading}
                            error={!!error}
                            helperText={error && error.includes('email') ? error : ''}
                        />
                        <TextField
                            fullWidth
                            label="ifre"
                            type="password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            margin="normal"
                            required
                            disabled={loading}
                            error={!!error}
                            helperText={error && error.includes('ifre') ? error : ''}
                        />
                        <TextField
                            fullWidth
                            label="ifre Tekrar"
                            type="password"
                            value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                            margin="normal"
                            required
                            disabled={loading}
                            error={!!error}
                            helperText={error && error.includes('elemiyor') ? error : ''}
                        />
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            sx={{ mt: 3 }}
                            disabled={loading}
                        >
                            {loading ? 'Kayt Yaplyor...' : 'Kayt Ol'}
                        </Button>
                        <Button
                            fullWidth
                            variant="text"
                            onClick={() => navigate('/login')}
                            sx={{ mt: 1 }}
                            disabled={loading}
                        >
                            Zaten hesabnz var m? Giri yapn
                        </Button>
                    </form>
                </Paper>
            </Box>
        </Container>
    );
};

export default Register;